*{padding:0;margin:0;font-family:'Montserrat', -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;line-height:1.6;font-size:18px;box-sizing:border-box}a{text-decoration:none}a:hover{text-decoration:underline}.form-info-container{align-self:center;display:flex;flex-direction:column;height:100%;justify-content:center;align-items:center;flex:1 1 150px}@media only screen and (max-width: 1360px){.form-info-container{height:auto;margin-bottom:10px}}@media only screen and (max-width: 1060px){.form-info-container{display:none}}.form-info-container img{width:80%}.form-info-container .info-text{font-size:1em;font-style:italic;margin-top:15px}.form-info-container .info-text:nth-of-type(1){margin-top:40px;transform:translate(-10px);transition:all 500ms ease-out}@media only screen and (max-width: 1180px){.form-info-container .info-text:nth-of-type(1){transform:translate(10px)}}.form-info-container .info-text:nth-of-type(2){transform:translate(40px)}

.form-button{position:relative;display:inline-block;line-height:45px;padding:0 2rem;color:#fff;text-decoration:none;letter-spacing:2px;font-size:1rem;border-radius:40px;border:none;overflow:hidden;background:linear-gradient(135deg, rgba(213,115,2,0.857878) 0%, #f27834 42%, #ff4800 96%);box-shadow:2px 2px 5px #7d7d7da1;transition:transform 0.2s ease-out}.form-button:focus{outline:none}.form-button:hover{cursor:pointer}.form-button:active{transform:scale(0.97)}.form-button:disabled{opacity:0.75;cursor:default}

input[type="text"],input[type="password"]{font-size:0.9rem;line-height:40px;padding:0 12px;width:270px;border-radius:40px;border:none;box-shadow:0px 0px 20px 0px #e09220;width:100%}input[type="text"]:focus,input[type="password"]:focus{outline:none}input[type="text"].error-input,input[type="password"].error-input{box-shadow:0px 0px 15px 0px #e10000b3}input[type="text"].correct-input,input[type="password"].correct-input{box-shadow:0px 0px 20px 0px #009f01b3}

*{padding:0;margin:0;font-family:'Montserrat', -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;line-height:1.6;font-size:18px;box-sizing:border-box}a{text-decoration:none}a:hover{text-decoration:underline}.form-control-container{flex:0 1 300px}@media only screen and (max-width: 1060px){.form-control-container{margin:0 20px;flex:1 1 300px}}@media only screen and (max-width: 505px){.form-control-container{margin:0 20px;flex:1 1 300px}}.form-control-container h1{font-size:1.5rem;text-align:center;margin-bottom:35px}.form-control-container h1 .company-title{font-size:inherit}@media only screen and (max-width: 1060px){.form-control-container h1 .company-title{display:none}}.form-control-container h1 .company-logo{display:none}@media only screen and (max-width: 1060px){.form-control-container h1 .company-logo{margin-left:5px;margin-top:10px;width:auto;height:1.5rem;display:inline-block}}form{display:flex;flex-direction:column;align-items:center}form label{font-size:0.8rem}form label[for="passwordInput"],form label[for="emailInput"]{align-self:start;margin-bottom:0.3rem;font-weight:600;margin-bottom:10px}.error-msg{width:100%;font-weight:500;margin-top:5px;font-size:0.7rem;color:#ff0000f0}label[for="passwordInput"]{margin-top:20px}.button-container{display:flex;align-items:center;justify-content:space-between;margin-top:40px;width:100%}@media only screen and (max-width: 605px){.button-container{flex-direction:column;margin-top:25px}}@media only screen and (max-width: 605px){.button-container .form-button{margin-top:15px;width:80%}}@media only screen and (max-width: 800px){.button-container .form-button{padding:0 20%}}.chk-container{font-size:0.8rem;display:block;position:relative;padding-left:25px;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}.chk-container input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.checkmark{position:absolute;top:0;left:0;height:20px;width:20px;border:1px solid orange}.chk-container:hover input ~ .checkmark{background-color:rgba(255,152,55,0.404)}.chk-container input:checked ~ .checkmark{background-color:orange}.checkmark:after{content:"";position:absolute;display:none}.chk-container input:checked ~ .checkmark:after{display:block}.chk-container .checkmark:after{left:5px;top:1px;width:5px;height:10px;border:solid white;border-width:0 3px 3px 0;transform:rotate(45deg)}

.logged-content-container{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:80vh}.logged-content-container .content-info{display:flex;align-items:center}.logged-content-container .content-info h1{margin-left:10px;font-size:1.3rem}.logged-content-container .logo{position:absolute;width:150px;top:20px;left:20px}

