.main-container{display:flex;justify-content:center;align-items:center;width:100vw;height:100vh}.main-container::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:#b11f1f url(/liveChat-form/static/media/bg.96e0ea17.jpg) no-repeat center;z-index:-1;background-size:cover;opacity:1}.main-container::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(142deg, rgba(213,2,2,0.857878) 0%, rgba(253,190,29,0.980978) 58%, #ff9e00 90%);z-index:-1;background-size:contain;opacity:0.8}

